* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;

    user-select: none;
    /* supported by Chrome and Opera */
    -webkit-user-select: none;
    /* Safari */
    -khtml-user-select: none;
    /* Konqueror HTML */
    -moz-user-select: none;
    /* Firefox */
    -ms-user-select: none;
    /* Internet Explorer/Edge */

}



html {

    overflow: hidden;
    width: 100vw;
    touch-action: none;
}

body {
    height: 100vh;

    background: rgb(175, 175, 175);
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
    /* iOS velocity scrolling */
    overscroll-behavior: contain;
}


.app {
    display: grid;
    grid-template-columns: 1fr 1fr 2fr;
    grid-template-rows: 1fr 9fr;
    grid-template-areas:
        'header header header'
        'helper exercise abacusholder';

    height: 80vh;
    width: 100vw;
    background: lightgray;
}

.header {
    background: rgb(175, 175, 175);
    grid-area: header;
    display: flex;
    justify-content: flex-start
}

.helper {
    background: lightgray;
    grid-area: helper;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.exercise {
    background: lightgray;
    grid-area: exercise;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: center;
    font-size: 10vmin;
    font-family: 'Rubik Mono One', sans-serif;
    padding-right: 20%
}


.abacusholder {
    background: rgb(175, 175, 175);
    grid-area: abacusholder;
}

.overlay {
    height: 100%;
    width: 100%;
    display: flex;
    position: fixed;
    align-items: center;
    justify-content: center;
}

.popupText {
    color:red;
    display: hidden;
}


.sweet-loading {
    height: 100%;
    width: 100%;
    display: flex;
    position: fixed;
    align-items: center;
    justify-content: center;
}